body {
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-size: 110%;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  /* box-shadow: 0 40px 80px #FBE4E0; */
  border: rgb(254, 238, 250);
  border-radius: 2rem;
  padding: 50px;
  box-sizing: border-box;
  margin: 0;
  z-index: 1;
}

label {
  padding-bottom: 4rem;
}

input[type=password] {
  margin: 10px;
  padding: 10px;
  font-size: 20px;
  border-radius: 5px rgb(255, 231, 249);
  outline: none;
  text-align: center;
}

