@media (max-width: 768px) {
  /* Styles for tablets and larger screens */
  .login-container {
    height: 100vh;
  }
}

body {
  display: flex;
  height: 100vh;

  align-items: center;
  justify-content: center;
}

.btn-liquid {
  display: inline-block;
  position: relative;
  width: 240px;
  height: 60px;
  border-radius: 2px;

  /* color: #fff; */
  color: rgb(255, 134, 229);
  font: 700 14px/60px "Droid Sans", sans-serif;
  letter-spacing: 0.05em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.btn-liquid .inner {
  position: relative;
  text-align: center;
  z-index: 4;
}

.btn-liquid canvas {
  position: absolute;
  top: -50px;
  right: -50px;
  bottom: -50px;
  left: -50px;

  z-index: 3;
}
